import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { CinchyService } from '../../Services/cinchy.service';

@Component({
  selector: 'app-client',
  templateUrl: './client.component.html',
  styleUrls: ['./client.component.css']
})
export class ClientComponent implements OnInit {

  clientForm: FormGroup;
  submitted = false;

  constructor(private formBuilder: FormBuilder, private cinchyService: CinchyService) { }

  ngOnInit(): void {

    this.cinchyService.getJSON().subscribe(data => {
      console.log(data);
    });

    this.clientForm = this.formBuilder.group({
      client: ['', Validators.required],
      firstName: [''],
      lastName: [''],
      bday: [''],
      email: [''],
      CompanyName: [''],
      position: [''],
      annualSalary: [''],
      desc: [''],
      favColor: [''],
      hobby: ['', Validators.required],
      datefield: ['', [Validators.required, Validators.email]],
      phone: ['', [Validators.required, Validators.minLength(10)]],
      confirmPassword: ['', Validators.required]
    });

  }

  get f() {
    return this.clientForm.controls;
  }


  onSubmit() {
    this.submitted = true;
    //return on errors
    if (this.clientForm.invalid) {
      return;
    }
  }
}
